import { Component } from 'react';
import shortid from 'shortid';

class Form extends Component {
  state = {
    name: '',
    tel: '',
    radioBtn: 'junior',
    check: false,
  };

  nameInputId = shortid.generate();
  telInputId = shortid.generate();

  handleChange = event => {
    // №3 this.setState({ [event.currentTarget.name]: event.currentTarget.value });
    const { name, value } = event.currentTarget;
    this.setState({ [name]: value });
    //console.log(event.currentTarget);
    //console.log(event.currentTarget.name);
    //console.log(event.currentTarget.value);
  };
  handleSubmit = event => {
    event.preventDefault(); //обязательно когда сабмит

    //console.log(this.state);
    this.props.onSubmitFormProps(this.state); //на верх в Арр
    this.reset();
  };

  handleCheckChange = event => {
    console.log(event.currentTarget.checked);

    this.setState({ check: event.currentTarget.checked });
  };

  reset = () => {
    this.setState({ name: '', tel: '' });
  };

  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <label htmlFor={this.nameInputId}>
          {/*если нужно связать lebel и input*/}
          Имя
          <input
            type="text"
            name="name"
            value={this.state.name}
            onChange={this.handleChange}
            id={this.nameInputId} //если нужно связать lebel и input
          />
        </label>
        <br />
        <label htmlFor={this.telInputId}>
          Телефон
          <input
            type="tel"
            name="tel"
            value={this.state.tel}
            onChange={this.handleChange}
            id={this.telInputId}
          />
        </label>
        <p>Ваш уровень</p>
        <label>
          <input
            type="radio"
            name="radioBtn"
            value="junior"
            onChange={this.handleChange}
            checked={this.state.radioBtn === 'junior'}
          />
          Junior
        </label>
        <label>
          <input
            type="radio"
            name="radioBtn"
            value="middle"
            onChange={this.handleChange}
            checked={this.state.radioBtn === 'middle'}
          />
          Middle
        </label>
        <label>
          <input
            type="radio"
            name="radioBtn"
            value="senior"
            onChange={this.handleChange}
            checked={this.state.radioBtn === 'senior'}
          />
          Senior
        </label>
        <br />
        <label>
          <input
            type="checkbox"
            name="check"
            checked={this.state.check}
            onChange={this.handleCheckChange}
          />
          Согласен с условием
        </label>
        <button type="submit" disabled={!this.state.check}>
          Отправить
        </button>
      </form>
    );
  }
}

export default Form;
