import React, { PureComponent } from 'react';
import classNames from 'classnames';
import './ColorPicker.css';

class ColorPicker extends PureComponent {
  state = {
    activeOptionInd: 0,
  };

  setActiveIdx = index => {
    this.setState({ activeOptionInd: index });
  };

  /* №1 makeOptionClassName = index => {
     const optionClasses = ['ColorPicker-title-option'];
    if (index === this.state.activeOptionInd) {
      optionClasses.push('ColorPicker-title-option-active');
    }
    return optionClasses.join(' ');*/
  /* №2 makeOptionClassName = index => {
    return classNames('ColorPicker-title-option', {
      'ColorPicker-title-option-active': index === this.state.activeOptionInd,
    });
  };*/

  render() {
    const { label } = this.props.options[this.state.activeOptionInd];
    //console.log(this.activeOption);

    return (
      <div className="ColorPicker">
        <h2 className="ColorPicker-title">Color Picker</h2>
        <p>Выбран цвет: {label}</p>
        <div>
          {/* {this.props.options.map(({ label, color }, index) => (
            <button
              type="button"
              key={label}
              className="ColorPicker-title-option"
              style={{
                backgroundColor: color,
                border:
                  index === this.state.activeOptionInd
                    ? '5px solid black'
                    : 'none',
              }}
            ></button>
            ))}*/}
          {this.props.options.map(({ label, color }, index) => (
            /*const optionClasses = ['ColorPicker-title-option'];
             if (index === this.state.activeOptionInd) {
              optionClasses.push('ColorPicker-title-option-active');
            }*/
            //const optionClassName = this.makeOptionClassName(index);
            // return (
            <button
              type="button"
              key={label}
              /* №2 className={this.makeOptionClassName(index)}*/
              className={classNames('ColorPicker-title-option', {
                //динамический класс
                'ColorPicker-title-option-active':
                  index === this.state.activeOptionInd,
              })}
              style={{ backgroundColor: color }}
              onClick={() => this.setActiveIdx(index)}
              // onMouseOver={() => this.setActiveIdx(index)}
            ></button>
          ))}
        </div>
      </div>
    );
  }
}
/*const ColorPicker = ({ options }) => {
  
};*/
export default ColorPicker;
