import React, { Component } from 'react';
import shortid from 'shortid';
import ColorPicker from '../components/ColorPicker';
//import Counter from '../components/Counter';
//import Dropdown from '../components/Dropdown';
//import Clock from '../components/Clock';
import Modal from '../components/Modal';
import TodoList from '../components/TodoList';
import TodoEditor from '../components/TodoEditor';
import Container from '../components/Container';
import initialTodos from '../components/todos.json';
import Form from '../components/Form';
import Filter from '../components/Filter';
//import Tabs from '../components/Tabs';
//import tabs from './tabs.json';
import IconButton from '../components/IconButton';
import { ReactComponent as AddIcon } from '../icons/add.svg';

const colorPickerOptions = [
  { label: 'red', color: '#F44336' },
  { label: 'green', color: '#4CAF50' },
  { label: 'blue', color: '#2196F3' },
  { label: 'grey', color: '#607D8B' },
  { label: 'pink', color: '#E91E63' },
  { label: 'indigo', color: '#3F51B5' },
];

class TodosView extends Component {
  state = {
    todos: initialTodos,
    filter: '',
    showModal: false,
  };

  componentDidMount() {
    //console.log('App componentDidMount');

    const todos = localStorage.getItem('todos');
    const parsedTodos = JSON.parse(todos);

    //console.log(parsedTodos);
    if (parsedTodos) {
      this.setState({ todos: parsedTodos });
    }
  }

  componentDidUpdate(prevProps, prevState) {
    // console.log('App componentDidUpdate');
    if (this.state.todos !== prevState.todos) {
      //console.log('Обновилось поле todos, записываю todos в хранилище.');
      localStorage.setItem('todos', JSON.stringify(this.state.todos));
    }
  }
  //componentWillUnmount(){}// переда уничтожением

  addTodo = text => {
    //добавляет нов todo
    // console.log(text);
    const todo = {
      id: shortid.generate(),
      text,
      completed: false,
    };

    this.setState(prevState => ({
      todos: [todo, ...prevState.todos],
    }));

    this.toggleModal(); //!!!!!
  };

  deleteTodo = todoId => {
    this.setState(prevState => ({
      todos: prevState.todos.filter(todo => todo.id !== todoId),
    }));
  };

  toggleCompleted = todoId => {
    console.log(todoId);

    this.setState(({ todos }) => ({
      todos: todos.map(todo =>
        todo.id === todoId ? { ...todo, completed: !todo.completed } : todo,
      ),
    }));
  };

  handleFormSubmit = data => {
    setTimeout(() => {
      console.log(data);
    }, 1000);
  }; //поднятия состояния

  handleChangeFilter = event => {
    this.setState({ filter: event.currentTarget.value });
  };

  getVisibleTodos = () => {
    const { filter, todos } = this.state;
    const normolizedFilter = filter.toLowerCase();

    return todos.filter(todo =>
      todo.text.toLowerCase().includes(normolizedFilter),
    );
  };

  getcompletedTodoCount = () => {
    const { todos } = this.state;
    return todos.reduce(
      (acc, todo) => (todo.completed ? acc + 1 : acc), //total вместо acc
      0,
    );
  };

  toggleModal = () => {
    this.setState(({ showModal }) => ({ showModal: !showModal }));
  };

  render() {
    //console.log('App render');
    const { todos, filter, showModal } = this.state;
    const totalTodoCount = todos.length;
    const completedTodoCount = this.getcompletedTodoCount();
    const visibleTodos = this.getVisibleTodos();

    return (
      <Container>
        <IconButton
          type="button"
          onClick={this.toggleModal}
          aria-label="Добавить todo"
        >
          <AddIcon width="40" hanging="40" fill="#fff" />
        </IconButton>
        {/* <Tabs items={tabs} />*/}
        {/*{showModal && <Clock />}*/}
        {/*<button type="button" onClick={this.toggleModal}>
              Открыть
           </button>*/}
        {showModal && (
          <Modal onClose={this.toggleModal}>
            {/* <h1>Привет это контент модалки как children</h1>
            <p>
              Lorem ipsum dolor sit amet consectetur adipisicing elit.
              Repudiandae alias rem amet architecto enim atque quos labore,
              tempora libero quia rerum sint, et quis voluptates minima. Quod
              obcaecati minima quo.
            </p>
            <button type="button" onClick={this.toggleModal}>
              x
           </button>*/}
            <TodoEditor onSubmit={this.addTodo} />
          </Modal>
        )}
        <ColorPicker options={colorPickerOptions} />
        <Form onSubmitFormProps={this.handleFormSubmit} />
        <div>
          <p>Общее кол-во: {totalTodoCount}</p>
          <p>Кол-во выполненых: {completedTodoCount}</p>
        </div>
        {/* <TodoEditor onSubmit={this.addTodo} />*/}
        <Filter value={filter} onChange={this.handleChangeFilter} />
        <TodoList
          todos={visibleTodos} //заменили{todos}
          onDeleteTodo={this.deleteTodo}
          onToggleCompleted={this.toggleCompleted}
        />
        *
      </Container>
    );
  }
}
export default TodosView;
